const pipeline = (x) => x();

const importers = [
  async (path) => {
    if (!/^\.[\\/](?:(?!\.)(?=.)[^/]*?\.stories\.mdx)$/.exec(path)) {
      return;
    }
  
    const pathRemainder = path.substring(2);
    return import(
      /* webpackChunkName: "[request]" */
      /* webpackInclude: /(?:(?!\.)(?=.)[^/]*?\.stories\.mdx)$/ */
      './' + pathRemainder
    );
  }
  ,
  async (path) => {
    if (!/^\.[\\/](?:(?!\.)(?=.)[^/]*?\.stories\.(js|jsx|ts|tsx))$/.exec(path)) {
      return;
    }
  
    const pathRemainder = path.substring(2);
    return import(
      /* webpackChunkName: "[request]" */
      /* webpackInclude: /(?:(?!\.)(?=.)[^/]*?\.stories\.(js|jsx|ts|tsx))$/ */
      './' + pathRemainder
    );
  }
  ,
  async (path) => {
    if (!/^\.[\\/](?:packages(?:\/(?!\.)(?:(?:(?!(?:^|\/)\.).)*?)\/|\/|$)(?!\.)(?=.)[^/]*?\.stories\.mdx)$/.exec(path)) {
      return;
    }
  
    const pathRemainder = path.substring(11);
    return import(
      /* webpackChunkName: "[request]" */
      /* webpackInclude: /(?:\/packages(?:\/(?!\.)(?:(?:(?!(?:^|\/)\.).)*?)\/|\/|$)(?!\.)(?=.)[^/]*?\.stories\.mdx)$/ */
      './packages/' + pathRemainder
    );
  }
  ,
  async (path) => {
    if (!/^\.[\\/](?:packages(?:\/(?!\.)(?:(?:(?!(?:^|\/)\.).)*?)\/|\/|$)(?!\.)(?=.)[^/]*?\.stories\.(js|jsx|ts|tsx))$/.exec(path)) {
      return;
    }
  
    const pathRemainder = path.substring(11);
    return import(
      /* webpackChunkName: "[request]" */
      /* webpackInclude: /(?:\/packages(?:\/(?!\.)(?:(?:(?!(?:^|\/)\.).)*?)\/|\/|$)(?!\.)(?=.)[^/]*?\.stories\.(js|jsx|ts|tsx))$/ */
      './packages/' + pathRemainder
    );
  }
  
];

export async function importFn(path) {
  for (let i = 0; i < importers.length; i++) {
    const moduleExports = await pipeline(() => importers[i](path));
    if (moduleExports) {
      return moduleExports;
    }
  }
}